/* reset css */
html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}

body {
  line-height: 1.2;
  font-family: "Overpass", sans-serif;
  font-size: 1.6rem;
}

ol,
ul {
  list-style: none;
}

blockquote,
q {
  quotes: none;
}

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

/* Custom Additions Below */
:root,
* {
  box-sizing: border-box;
}

:root {
  font-size: 62.5%;
}

a,
a:hover,
a:focus,
a:active {
  text-decoration: none;
  color: inherit;
}

button {
  border: none;
  outline: none;
  background: none;
  cursor: pointer;
  font-family: "Ubuntu", sans-serif;
  font-size: 1.7rem;
  letter-spacing: 0.05rem;
  border-radius: 3rem;
}

img {
  display: block;
}

h2 {
  font-weight: 400;
  color: #1f3f5b;
}

p {
  color: #4b5862;
  line-height: 1.8;
  font-weight: 200;
  letter-spacing: 0.05rem;
}

body {
  width: 100%;
  height: 100vh;
  /* header styling */
  /* Designed for the future styling */
}
body header {
  background-image: url(/images/bg-pattern-intro.svg);
  background-repeat: no-repeat;
  background: linear-gradient(to right, #ff8f70, #ff3d54);
  border-bottom-left-radius: 10rem;
}
body header .header__container {
  padding: 5.5rem 2.4rem 14.5rem 2.4rem;
}
body header .header__container nav.header__nav {
  position: relative;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}
body header .header__container nav.header__nav img.logo {
  width: 8rem;
  height: 3rem;
}
body header .header__container nav.header__nav button img.burger, body header .header__container nav.header__nav button img.close {
  margin-top: 0.5rem;
}
body header .header__container nav.header__nav button#close {
  display: none;
}
body header .header__container nav.header__nav button img.burger {
  width: 3rem;
  height: 1.7rem;
}
body header .header__container nav.header__nav button img.close {
  width: 2.4rem;
  height: 2.4rem;
}
body header .header__container nav.header__nav ul.nav__nav-list {
  position: absolute;
  display: flex;
  flex-direction: column;
  padding: 2.5rem;
  width: 100%;
  top: 7rem;
  border-radius: 0.5rem;
  background-color: white;
  text-align: center;
  display: none;
}
body header .header__container nav.header__nav ul.nav__nav-list.show {
  display: flex;
}
body header .header__container nav.header__nav ul.nav__nav-list li.nav__nav-item {
  margin-bottom: 2.5rem;
  font-size: 1.9rem;
}
body header .header__container nav.header__nav ul.nav__nav-list li.nav__nav-item .nav__button-icon {
  display: flex;
  align-items: center;
  margin: 0 10rem;
  /* JS */
}
body header .header__container nav.header__nav ul.nav__nav-list li.nav__nav-item .nav__button-icon button img {
  margin: 0 0 0.2rem 0.5rem;
  width: 1rem;
  height: 0.8rem;
}
body header .header__container nav.header__nav ul.nav__nav-list li.nav__nav-item .nav__button-icon button img button img.light-arrow-icon {
  display: none;
}
body header .header__container nav.header__nav ul.nav__nav-list li.nav__nav-item .nav__button-icon button.open img {
  transform: rotate(540deg);
  transition: transform 0.5s;
}
body header .header__container nav.header__nav ul.nav__nav-list li.nav__nav-item ul.nav__dropdown {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2rem;
  margin-top: 2rem;
  padding: 2.7rem 9rem;
  border-radius: 0.5rem;
  font-size: 1.6rem;
  color: #4b5862;
  background-color: #efeff1;
  display: none;
  /* JS */
}
body header .header__container nav.header__nav ul.nav__nav-list li.nav__nav-item ul.nav__dropdown.show {
  display: flex;
}
body header .header__container nav.header__nav ul.nav__nav-list li.nav__nav-item ul.nav__dropdown li:not(:last-child) {
  margin-bottom: 0.5rem;
}
body header .header__container nav.header__nav hr {
  margin: 0.1rem 0 3.2rem 0.6rem;
  border: 0;
  height: 0.1rem;
  width: 100%;
  background: #efeff1;
}
body header .header__container nav.header__nav a.nav__sign-up button {
  padding: 1.4rem 3.5rem;
  font-weight: 500;
  color: white;
  background: linear-gradient(to right, #ff8f70, #ff3d54);
}
body header .header__container .header__content {
  padding: 11rem 0.5rem 0 0.5rem;
  text-align: center;
  color: white;
}
body header .header__container .header__content h1.header__heading {
  margin-bottom: 1rem;
  font-size: 3.65rem;
  font-weight: 300;
}
body header .header__container .header__content p.header__description {
  margin-bottom: 4rem;
  font-size: 1.7rem;
  font-weight: 100;
}
body header .header__container .header__content a button {
  padding: 1.4rem 2.2rem;
  font-size: 1.5rem;
  font-weight: 500;
}
body header .header__container .header__content a button.start-for-free {
  margin-right: 1rem;
  background-color: white;
  color: #ff525d;
}
body header .header__container .header__content a button.learn-more {
  border: 0.1rem solid white;
  background-color: transparent;
  color: white;
}
body section.future {
  outline: 1px solid red;
  padding: 10.5rem 1rem 9.5rem 1rem;
  text-align: center;
  background-image: url(/images/illustration-editor-mobile.svg);
  background-repeat: no-repeat;
  background-position: top 18rem center;
}
body section.future h2 {
  font-size: 2.8rem;
}
body section.future .future__container {
  display: flex;
  flex-direction: column;
}
body section.future .future__container .future__container-right {
  margin-top: 36.5rem;
}
body section.future .future__container .future__container-left {
  display: flex;
  flex-direction: column;
  gap: 3.5rem;
  margin-top: 4.7rem;
  padding: 0 2.5rem;
}
body section.future .future__container .future__container-left .container-left__inner {
  display: flex;
  flex-direction: column;
  gap: 1.3rem;
}

@media screen and (min-width: 1440px) {
  body {
    /* header styling */
    /* Designed for the future styling */
  }
  body header .header__container {
    padding: 6rem 17rem 14.5rem 17rem;
  }
  body header .header__container nav.header__nav img.logo {
    width: 10rem;
    height: 4rem;
  }
  body header .header__container nav.header__nav button#burger {
    display: none;
  }
  body header .header__container nav.header__nav ul.nav__nav-list {
    all: unset;
    display: flex;
    flex-direction: row;
    list-style: none;
    justify-content: space-between;
    width: 87%;
  }
  body header .header__container nav.header__nav ul.nav__nav-list .nav__container-left, body header .header__container nav.header__nav ul.nav__nav-list .nav__container-right {
    display: flex;
    margin-top: 1rem;
  }
  body header .header__container nav.header__nav ul.nav__nav-list .nav__container-left button.nav-button, body header .header__container nav.header__nav ul.nav__nav-list .nav__container-right button.nav-button {
    font-size: 1.5rem;
    color: white;
  }
  body header .header__container nav.header__nav ul.nav__nav-list .nav__container-left {
    margin-left: 2rem;
  }
  body header .header__container nav.header__nav ul.nav__nav-list .nav__container-left li.nav__nav-item:hover {
    text-decoration: underline;
    color: white;
  }
  body header .header__container nav.header__nav ul.nav__nav-list .nav__container-left li.nav__nav-item .nav__button-icon {
    margin: unset;
    /* JS */
  }
  body header .header__container nav.header__nav ul.nav__nav-list .nav__container-left li.nav__nav-item .nav__button-icon button img {
    margin: 0 2.5rem -0.1rem -1rem;
  }
  body header .header__container nav.header__nav ul.nav__nav-list .nav__container-left li.nav__nav-item .nav__button-icon button img.light-arrow-icon {
    display: block;
  }
  body header .header__container nav.header__nav ul.nav__nav-list .nav__container-left li.nav__nav-item .nav__button-icon button img.dark-arrow-icon {
    display: none;
  }
  body header .header__container nav.header__nav ul.nav__nav-list .nav__container-left li.nav__nav-item .nav__button-icon button img:hover {
    transform: rotate(180deg);
    transition: transform 0.5s;
  }
  body header .header__container nav.header__nav ul.nav__nav-list .nav__container-left li.nav__nav-item ul.nav__dropdown {
    position: absolute;
    align-items: flex-start;
    padding: 2.7rem 5.5rem 2.7rem 2.7rem;
    font-size: 1.5rem;
  }
  body header .header__container nav.header__nav ul.nav__nav-list .nav__container-left li.nav__nav-item ul.nav__dropdown > li:hover {
    font-weight: 600;
  }
  body header .header__container nav.header__nav ul.nav__nav-list .nav__container-left li.nav__nav-item ul.dropdown__product {
    left: 14rem;
  }
  body header .header__container nav.header__nav ul.nav__nav-list .nav__container-left li.nav__nav-item ul.dropdown__company {
    left: 24.87rem;
  }
  body header .header__container nav.header__nav ul.nav__nav-list .nav__container-left li.nav__nav-item ul.dropdown__connect {
    left: 36.3583rem;
  }
  body header .header__container nav.header__nav ul.nav__nav-list .nav__container-right a.nav__sign-up button {
    margin: -1.1rem 0 0 3rem;
    font-size: 1.5rem;
    color: #ff525d;
    background: white;
  }
  body header .header__container nav.header__nav ul.nav__nav-list .nav__container-right a.nav__sign-up button:hover {
    color: white;
    background: #f97b86;
    opacity: 0.9;
  }
  body header .header__container nav.header__nav hr {
    display: none;
  }
  body header .header__container .header__content h1.header__heading {
    font-size: 6rem;
    font-weight: 500;
  }
  body header .header__container .header__content p.header__description {
    font-size: 2.1rem;
  }
  body header .header__container .header__content a button.start-for-free:hover {
    color: white;
    background: #f97b86;
    opacity: 0.9;
  }
  body header .header__container .header__content a button.learn-more:hover {
    color: #f97b86;
    background: white;
  }
  body section.future {
    background-image: url(/images/illustration-editor-desktop.svg);
    background-position: top 7rem right -27rem;
    padding: 15.5rem 1rem 17.2rem 1rem;
  }
  body section.future h2.future__heading {
    font-size: 3.7rem;
    font-weight: 500;
  }
  body section.future .future__container {
    flex-direction: row-reverse;
    margin: 5.8rem 0 0 13.5rem;
  }
  body section.future .future__container .future__container-right {
    flex: 1;
    padding: 0 19.3rem 0 2.5rem;
  }
  body section.future .future__container .future__container-left {
    flex: 1;
    gap: 7rem;
  }
  body section.future .future__container .future__container-left .container-left__inner {
    gap: 2.5rem;
    text-align: left;
  }
}